import { Solution } from '..';
export class UIGraph {
    app: Solution;

    constructor(app: Solution) {
        this.app = app;
        this.initUI();
    }
    public initUI() {
        const divMenu = document.createElement('div');
        divMenu.style.width = '160px';
        divMenu.style.position = 'absolute';
        divMenu.style.background = '#222';
        divMenu.style.padding = '10px';
        this.app.divHost.appendChild(divMenu);


        const divRemove = document.createElement('button');
        divRemove.textContent = 'remove';
        divRemove.onclick = () => {
            this.app.controller.common.removeSelection();
        }
        divMenu.appendChild(divRemove);

        const divText = document.createElement('div');
        divText.textContent = 'N:0, E:0';
        divText.style.color = '#bbb';
        divMenu.appendChild(divText);
        this.app.controller.divLog = divText;

        const divHist = document.createElement('div');
        divHist.textContent = 'hist: 0';
        divHist.style.color = '#bbb';
        divMenu.appendChild(divHist);
        this.app.controller.divHist = divHist;

        const divSel = document.createElement('div');
        divSel.textContent = 'undefined';
        divSel.style.color = '#bbb';
        divMenu.appendChild(divSel);
        this.app.controller.divSel = divSel;

        const divDivider0 = getDivider();
        divMenu.appendChild(divDivider0);


        // .....................................................


        // .....................................................

        const divDelaunay = getCheckBox('Delaunay', (v) => { this.app.controller.common.toggleDelaunay(v); })
        divMenu.appendChild(divDelaunay.div);

        const divMST = getCheckBox('MST', (v) => { this.app.controller.common.toggleMST(v); })
        divMenu.appendChild(divMST.div);

        const divDFS = getBtn('DFS', () => { this.app.controller.common.computedDFS(); })
        divMenu.appendChild(divDFS);

        const divBFS = getBtn('BFS', () => { this.app.controller.common.computedBSF(); })
        divMenu.appendChild(divBFS);

        const divSearchResult = document.createElement('div');
        const divSearchResultP = document.createElement('p');
        divSearchResultP.style.marginTop = '2px';
        divSearchResult.appendChild(divSearchResultP);
        divMenu.appendChild(divSearchResult);
        this.app.controller.divSearchResult = divSearchResultP;


        const divDijkstra = getCheckBox('Dijkstra', (v) => { this.app.controller.common.computedDijkstra(v); })
        divMenu.appendChild(divDijkstra.div);
        const divAStar = getCheckBox('*Star', (v) => { this.app.controller.common.computedAStar(v); })
        divMenu.appendChild(divAStar.div);

        const divStart = getBtn('Start', () => { this.app.controller.common.graphSystem.setNodeStart(); });
        divMenu.appendChild(divStart);
        const divEnd = getBtn('End', () => { this.app.controller.common.graphSystem.setNodeEnd(); });
        divMenu.appendChild(divEnd);


        const divOffscreen = getCheckBox('Offscreen', (v) => { this.app.controller.common.toggleOffscreenBind(v); })
        divMenu.appendChild(divOffscreen.div);

        // const divSearchResult = document.createElement('div');
        // const divSearchResultP = document.createElement('p');
        // divSearchResultP.style.marginTop = '2px';
        // divSearchResult.appendChild(divSearchResultP);
        // divMenu.appendChild(divSearchResult);
        // this.app.controller.divSearchResult = divSearchResultP;


        const divDivider = getDivider();
        divMenu.appendChild(divDivider);


        const divDrawing = getDrawingModeRadio(['Selection', 'Drawing Line', 'Drawing Pline'], (v) => {
            console.log({ v });
            this.app.controller.common.setCommandMode(v);
        });
        divMenu.appendChild(divDrawing);



        // .....................................................

        const containerA = document.createElement('div');
        containerA.style.background = '#111';
        containerA.style.margin = '5px';
        containerA.style.height = '100px';
        // containerA.style.display = 'grid';
        divMenu.appendChild(containerA);

        const undoBtn = getBtn('undo', () => { this.app.controller.common.undo(); })
        containerA.appendChild(undoBtn);

        const redoBtn = getBtn('redo', () => { this.app.controller.common.redo(); })
        containerA.appendChild(redoBtn);


        const btnExport = document.createElement('button');
        btnExport.textContent = 'Export JSON';
        containerA.appendChild(btnExport);
        btnExport.onclick = () => {
            this.app.controller.exportJSON();
        }

        const resetBtn = getBtn('reset', () => { this.app.controller.common.reset(); })
        containerA.appendChild(resetBtn);

        const divBtn = document.createElement('button');
        divBtn.textContent = 'run';
        divBtn.onclick = () => {
            this.app.controller.needPopulate = !this.app.controller.needPopulate;
        }
        containerA.appendChild(divBtn);


        // .....................................................
    }
}

const getBtn = (title: string, fn: () => void) => {
    const divBtn = document.createElement('button');
    divBtn.textContent = title;
    divBtn.onclick = () => { fn(); }
    return divBtn;
};
export const getCheckBox = (title: string, fn: (v: boolean) => void) => {
    const div = document.createElement('div');
    div.style.display = 'flex';

    const checkBox = document.createElement('input');
    checkBox.type = 'checkbox';
    checkBox.defaultChecked = false;
    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(checkBox);
    div.appendChild(text);
    checkBox.onchange = () => fn(checkBox.checked);
    return {
        div, checkBox
    };
};

const getDivider = (color = '#fff', lineWidth = 1) => {
    const div = document.createElement('div');
    div.style.background = color;
    div.style.height = lineWidth + 'px';
    return div;
};


const getDrawingModeRadio = (radioValues = ['A', 'B', 'C'], fn: (v: string) => void) => {

    const container = document.createElement('div');
    container.style.background = '#111';
    container.style.margin = '5px';
    container.style.height = '100px';
    container.style.display = 'grid';

    const title = document.createElement('div');
    title.textContent = 'Command Mode';
    container.appendChild(title);
    // container

    radioValues.forEach(value => {
        const label = document.createElement('label');
        label.className = 'radio-item';

        const radio = document.createElement('input');
        radio.type = 'radio';
        radio.name = 'ratio';
        radio.value = value;

        const text = document.createTextNode(value);
        label.appendChild(radio);
        label.appendChild(text);
        container.appendChild(label);

        // Add event listener to handle radio button change
        radio.addEventListener('change', function () {
            // if (this.value === 'a') {
            //     title.textContent = 'Command Mode' + ': Section'
            // } else if (this.value === 'b') {
            //     title.textContent = 'Command Mode' + ': Line'
            // } else if (this.value === 'c') {
            //     title.textContent = 'Command Mode' + ': Polyline from the closest node'
            // }
            fn(this.value)
        });
    });

    return container;
}