import { RendererCanvas } from '../../../../../lib';
import { Solution } from '..';
export class CanvasRenderer extends RendererCanvas {
    public app: Solution;

    public time: number = 0.0;
    public pts: number[][] = [];

    public offscreen: OffscreenCanvas;
    constructor(div: any, app: Solution) {
        super(div);
        this.app = app;
        this.canvas.id = 'mapboxCustom'
        this.canvas.style.position = 'absolute';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';
        this.canvas.style.pointerEvents = 'none';

        this.offscreen = new OffscreenCanvas(this.canvas);

        this.start();
    }
    public mouseDown = (x: number, y: number): void => {

    }
    public mouseUp = (x: number, y: number): void => {

    }
    public mouseDrag = (x: number, y: number): void => {

    }
    public render(ctx: CanvasRenderingContext2D): void {
        this.time += 0.1;

        this.offscreen.reset();

        this.app.controller && this.app.controller.render(ctx, this.offscreen.ctx);

    }
}

class OffscreenCanvas {
    public refCanvas: HTMLCanvasElement;
    public canvas: HTMLCanvasElement;
    public ctx: CanvasRenderingContext2D;
    constructor(canvas) {
        this.refCanvas = canvas;
        this.canvas = document.createElement('canvas');
        this.ctx = this.canvas.getContext('2d');

        this.canvas.style.background = 'rgba(255, 0, 0, 0.12)';
        this.canvas.style.zIndex = '10';
        this.canvas.style.position = 'absolute';
        this.canvas.style.pointerEvents = 'none';
        this.canvas.style.display = 'none';

        window.addEventListener('resize', this.resize);
        // this.canvas.width = canvas.width;
        // this.canvas.height = canvas.height;
        this.resize();
        this.appendCanvas();
    }
    private appendCanvas() {

        this.refCanvas.parentElement.appendChild(this.canvas);
    }
    public getPix = (x: number, y: number) => {
        return this.ctx.getImageData(x,y,1,1).data;
    }
    private resize = () => {
        const bounding = this.refCanvas.getBoundingClientRect();
        this.canvas.width = bounding.width;
        this.canvas.height = bounding.height;
    }
    public destroy() {
        window.removeEventListener('resize', this.resize);
        try {
            while (this.refCanvas !== undefined && this.refCanvas.lastElementChild !== undefined) {
                this.refCanvas.removeChild(this.refCanvas.lastElementChild);
            }
        } catch (error) { }
    }
    public reset() {
        this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
    }
}