import { Controller } from '..';
import { Node, Edge } from '../../GraphSystem';
import { getDelaunay } from '../../GraphSystem/Algorithm/Delaunay';

export class GraphRenderer {
    public controller: Controller;

    public toggleDelaunay = false;
    public toggleMST = false;

    public toggleDijkstra = false;
    public toggleAStar = false;

    constructor(controller: Controller) {
        this.controller = controller;
    }
    public projection(x: number, y: number) {
        return this.controller.projection(x, y);
    }
    public render(ctx: CanvasRenderingContext2D, ctxOff: CanvasRenderingContext2D) {





        // this.graph.render(ctx);
        // this.divSel.textContent = JSON.stringify(this.graphSystem.captured);

        if (this.controller.graphSystem.captured) {
            this.controller.divSel.textContent = this.controller.graphSystem.captured.type;
        } else {
            this.controller.divSel.textContent = 'undefined';
        }

        this.renderNodeStartEnd(ctx, ctxOff);

        ctx.beginPath();
        ctx.arc(this.controller.m.x, this.controller.m.y, 2, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fillStyle = '#888';
        ctx.fill();


        if (this.toggleMST) {

            this.controller.common.computeMST();

            // if (this.controller.graphSystem.pathMST) {
            const nodes = this.controller.graphSystem.graph.nodes;

            // console.log('========');
            ctx.strokeStyle = '#ff0';
            ctx.beginPath();
            this.controller.graphSystem.pathMST.forEach(e => {
                const v0 = this.controller.common.toScreen(nodes[e.n0].vec.x, nodes[e.n0].vec.y);
                const v1 = this.controller.common.toScreen(nodes[e.n1].vec.x, nodes[e.n1].vec.y);
                ctx.moveTo(v0[0], v0[1]);
                ctx.lineTo(v1[0], v1[1]);
            });
            ctx.lineWidth = 5;
            ctx.stroke();
            // }
        }

        if (this.toggleDelaunay) {
            getDelaunay(ctx, this.controller.graphSystem.graph.nodes, this.controller.common.toScreen);
        }

        if (this.toggleDijkstra && this.controller.graphSystem.nodeStart && this.controller.graphSystem.nodeEnd) {
            const paths = this.controller.graphSystem.computePath();
            const pathProjected = paths.map((v) =>
                this.controller.common.toScreen(v.vec.x, v.vec.y)
            );

            ctx.beginPath();
            ctx.moveTo(pathProjected[0][0], pathProjected[0][1]);
            for (let i = 1; i < pathProjected.length; ++i) ctx.lineTo(pathProjected[i][0], pathProjected[i][1]);
            ctx.strokeStyle = '#f0f';
            ctx.lineWidth = 5;
            ctx.stroke();
        }

        if (this.toggleAStar && this.controller.graphSystem.nodeStart && this.controller.graphSystem.nodeEnd) {
            const pathProjected = this.controller.graphSystem.computePathAStar().map((v) =>
                this.controller.common.toScreen(v.x, v.y)
            );

            ctx.beginPath();
            ctx.moveTo(pathProjected[0][0], pathProjected[0][1]);
            for (let i = 1; i < pathProjected.length; ++i) ctx.lineTo(pathProjected[i][0], pathProjected[i][1]);
            ctx.strokeStyle = '#f0f';
            ctx.lineWidth = 5;
            ctx.stroke();
        }

        this.renderGraph(ctx, ctxOff);

    }
    public renderNodeStartEnd(ctx, ctxOff) {
        ctx.fillStyle = '#0ff';
        if (this.controller.graphSystem.nodeStart) {
            const v = this.controller.graphSystem.nodeStart.vec;
            ctx.beginPath();
            ctx.arc(v.x, v.y, 8, 0, Math.PI * 2.0);
            ctx.closePath();
            ctx.fill();
        }
        if (this.controller.graphSystem.nodeEnd) {
            const v = this.controller.graphSystem.nodeEnd.vec;
            ctx.beginPath();
            ctx.arc(v.x, v.y, 8, 0, Math.PI * 2.0);
            ctx.closePath();
            ctx.fill();
        }
    }
    public renderGraph(ctx: CanvasRenderingContext2D, ctxOff: CanvasRenderingContext2D) {

        this.controller.log = `N:${this.controller.graphSystem.graph.nodes.length}, E:${this.controller.graphSystem.graph.edges.length}`;
        // console.log(`N:${this.controller.graphSystem.graph.nodes.length}, E:${this.controller.graphSystem.graph.edges.length}`)

        this.controller.graphSystem.graph.edges.forEach((e: Edge) => { this.renderGraphEdge(ctx, ctxOff, e); })
        this.controller.graphSystem.graph.nodes.forEach((n: Node) => { this.renderGraphNode(ctx, ctxOff, n); })
    }
    public renderGraphNode(ctx: CanvasRenderingContext2D, ctxOff: CanvasRenderingContext2D, node: Node) {

        let p = this.projection(node.vec.x, node.vec.y);

        ctx.beginPath();
        ctx.arc(p[0], p[1], node.r, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fillStyle = '#ff0000';
        ctx.fill();
        if (node.isSelected) {
            ctx.lineWidth = 3;
            ctx.strokeStyle = '#00f';
            ctx.stroke();
        }

        ctx.strokeStyle = '#ff0000';
        ctx.lineWidth = 1;
        ctx.strokeText(`${node.networkDistance}`, p[0], p[1] - 20);
        ctx.strokeText(`${node.id}`, p[0], p[1] - 7);

        // Offscreen
        ctxOff.beginPath();
        ctxOff.arc(p[0], p[1], node.r + 1, 0, 3.14 * 2);
        ctxOff.closePath();
        ctxOff.fillStyle = `rgb(${node.color.join()})`;
        ctxOff.fill();

    }
    public renderGraphEdge(ctx: CanvasRenderingContext2D, ctxOff: CanvasRenderingContext2D, edge: Edge) {


        if (edge.isSelected) {
            ctx.lineWidth = 3;
            ctx.strokeStyle = '#00f';
            ctx.beginPath();
            let p = this.projection(edge.n0.vec.x, edge.n0.vec.y);
            ctx.moveTo(p[0], p[1]);

            p = this.projection(edge.n1.vec.x, edge.n1.vec.y);
            ctx.lineTo(p[0], p[1]);
            ctx.stroke();
        }
        ctx.lineWidth = 1;
        ctx.beginPath();
        let p = this.projection(edge.n0.vec.x, edge.n0.vec.y);
        ctx.moveTo(p[0], p[1]);

        p = this.projection(edge.n1.vec.x, edge.n1.vec.y);
        ctx.lineTo(p[0], p[1]);
        ctx.strokeStyle = '#0f0';
        ctx.stroke();

        ctx.strokeStyle = '#00ff00';
        ctx.lineWidth = 1
        const x = edge.n0.vec.x + (edge.n1.vec.x - edge.n0.vec.x) * 0.5
        const y = edge.n0.vec.y + (edge.n1.vec.y - edge.n0.vec.y) * 0.5
        p = this.projection(x, y);
        ctx.strokeText(`${edge.id}`, p[0], p[1] - 7)
    }
}


const projectionVs = (vs: any, toScreen) => {
    return vs.map((v) => {
        console.log(v)
        // toScreen(v.vec.x, v.vec.y)

        return toScreen(v.vec.x, v.vec.y);
    })
}