import { Delaunay, NVector3 } from '../../../../../../lib/NJSCore';

export const getDelaunay = (ctx, vs, toScreen) => {
    const delaunay = new Delaunay(vs.map((v) => {
        // console.log(v.vec);
        const sp = toScreen(v.vec.x, v.vec.y);
        return new NVector3(sp[0], sp[1]);
    }));
    // console.log(delaunay)
    delaunay.DrawTriClass(ctx, '#fff');
}