
import { MapboxGLWrapper } from './MapboxGLWrapper';
import { CanvasRenderer } from './CanvasRenderer';
import { Controller } from './Controller';
import { UIGraph } from './UIGraph';

export class Solution {
    public divHost: HTMLDivElement;
    public mapboxWrapper: MapboxGLWrapper;
    public renderer: CanvasRenderer;

    public controller: Controller;
    public ui: UIGraph;

    constructor(id: string) {
        this.divHost = document.getElementById(id) as HTMLDivElement;
        this.divHost.style.position = 'relative';

        this.mapboxWrapper = new MapboxGLWrapper(this.divHost.id);
        this.mapboxWrapper.mouseDown = this.mouseDown
        this.mapboxWrapper.mouseUp = this.mouseUp
        this.mapboxWrapper.mouseClick = this.mouseClick
        this.mapboxWrapper.mouseMove = this.mouseMove

        setTimeout(() => {
            this.renderer = new CanvasRenderer(this.divHost, this);
            

            this.controller = new Controller(this.renderer.canvas);
            this.controller.projection = (x: number, y: number) => {return this.mapboxWrapper.toScreen(x,y)}
            // this.controller.mouseClick(-73.97646684462907, 40.76334819495341);
    
            this.ui = new UIGraph(this);
            this.initPost();
        }, 100);

    }

    public mouseDown = (e) => {
        // TODO
        this.controller.mouseDown(e['lngLat']['lng'], e['lngLat']['lat'], e)

        if (this.controller.graphSystem.captured) {
            this.mapboxWrapper.map.dragPan.disable();
        }
    }
    public mouseUp = (e) => {
        // this.controller.mouseUp(e['lngLat']['lng'], e['lngLat']['lat'])
        // TODO
        this.controller.mouseUp()
    }
    public mouseDrag = (e) => {
        // console.log('drag');
        this.mapboxWrapper.map.dragPan.disable();
        this.controller.mouseDrag(e['lngLat']['lng'], e['lngLat']['lat'])
        // TODO
    }
    public mouseClick = (e) => {
        // TODO
        // console.log(e);
        // console.log(e['lngLat']);
        // this.renderer.pts.push([e['lngLat']['lng'], e['lngLat']['lat']])
        this.controller.mouseClick(e['lngLat']['lng'], e['lngLat']['lat'], e)
    }
    public mouseMove = (e)  =>{
        // TODO
        if (this.controller.pressed) {
            this.mouseDrag(e);
            return;
        } else {
            this.mapboxWrapper.map.dragPan.enable();
        }
        // console.log('move');
        this.controller.mouseMove(e['lngLat']['lng'], e['lngLat']['lat'])
    }
    public keyDown(k: KeyboardEvent) {
        // TODO
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
    public initPost() {

        this.controller.common.toggleOffscreenBind = (v) => {
            if (v) this.renderer.offscreen.canvas.style.display = 'block';
            else this.renderer.offscreen.canvas.style.display = 'none';
        }
        this.controller.common.graphSystem.getPix = (x: number, y: number): Uint8ClampedArray => {
            return this.renderer.offscreen.getPix(x, y);
        }
    }
}
