import { GraphSystem, Edge, Node } from '../GraphSystem';
import { Vector3, distance } from '../GraphSystem/Graph/util'
import { Common } from './Common';
import { GraphRenderer } from './Renderer';

// import { Graph, Vector3,  } from './GraphSystem';

export class Controller {
    public common: Common;
    public graphSystem: GraphSystem;
    public renderer: GraphRenderer;

    public commandMode: string = 'drawing'

    public canvas: HTMLCanvasElement;

    public divHist: HTMLDivElement; // = document.createElement('div');
    public divLog: HTMLDivElement;
    public divSel: HTMLDivElement;
    public divSearchResult: HTMLDivElement;

    public needPopulate = false;
    public m = new Vector3(0, 0, 0);
    public log: string = '';

    private wasMoved = false;
    public pressed = false;

    constructor(canvas: HTMLCanvasElement) {
        this.graphSystem = new GraphSystem();
        this.common = new Common(this)
        this.renderer = new GraphRenderer(this)
        this.canvas = canvas;
    }
    public projection = (x: number, y: number) => {
        return [x, y]
    }
    // ..........................................................
    public mouseDown(x: number, y: number, e: any): void {
        // console.log('down', e.point);
        const node = this.graphSystem.findNodeVec(e.point.x, e.point.y);
        // console.log({ node });

        if (this.graphSystem.captured && this.graphSystem.captured !== node) {
            this.graphSystem.nodePre = this.graphSystem.captured;
        }
        // console.log('down capture', node);
        this.graphSystem.captured = node;


        // console.log('down', this.graphSystem.captured)

        this.pressed = true;
    }
    public mouseClick(x: number, y: number, e: any): void {
        // console.log('click', x, y);
        this.graphSystem.captured = this.graphSystem.findNodeVec(e.point.x, e.point.y);
        // console.log('click', this.graphSystem.captured)
        // debugger

        // // add edge between selected node and pre-selected node
        // if (this.graphSystem.nodePre && this.graphSystem.captured) {
        //     // console.log('connection needed')
        //     this.graphSystem.addEdge(this.graphSystem.nodePre as Node, this.graphSystem.captured as Node);
        //     this.common.capture();
        //     this.updatePost()
        //     return;
        // }


        // // add a node on edge
        // const closeEdge = this.graphSystem.findEdgeVec(x, y);
        // if (closeEdge && this.graphSystem.captured === undefined) {
        //     const node = this.graphSystem.addNode(closeEdge.vec.x, closeEdge.vec.y);
        //     this.graphSystem.edgeSplitByNode(node, closeEdge.edge);
        //     this.common.capture();
        //     this.updatePost()
        //     return;
        // }


        // // click on canvas for adding a node with closest node
        // console.log(this.graphSystem.captured)
        // // this.graphSystem.addNode(x, y)

        if (this.graphSystem.captured === undefined) {
            this.common.addNodeWithClosestNode(x, y)
            this.common.capture();
            this.updatePost()
            return;
        }
    }

    public mouseMove(x: number, y: number): void {
        // console.log('move');
        this.m.x = x;
        this.m.y = y;
    }
    public mouseUp() {
        // console.log('up');
        // this.graphSystem.captured = undefined;
        let needCapture = false;


        if (this.graphSystem.captured && this.graphSystem.captured.type === 'node') {
            const node = this.graphSystem.captured as Node;
            // console.log('up', node)

            // Edge split from a moved node by it's edges
            needCapture = this.graphSystem.edgeSplitByNodeEdges(node);


            // // Add a node by edge intersection
            // node.edges.forEach((e, i) => {
            //     // setTimeout(() => {
            //     console.log(node.edges.length)
            //     this.graphSystem.edgeSplitByEdge(e);
            //     // }, 100);
            // })


            // merge two node into one node
            const other = this.graphSystem.findNode(node);
            if (other) {
                const v0 = this.common.toScreen(node.vec.x, node.vec.y);
                const v1 = this.common.toScreen(other.vec.x, other.vec.y);
                const dis = distance(new Vector3(v0[0], v0[1], 0), new Vector3(v1[0], v1[1], 0));
                if (dis < 16) {
                    this.graphSystem.mergeTwoNodes(other, node);
                    needCapture = true;
                }
            }

            // detect move for capturing scene states
            if (this.wasMoved) {
                this.wasMoved = false;
                needCapture = true;
            }

            this.graphSystem.captured = undefined;
            this.updatePost();
        }

        // update undo redo history
        needCapture && this.common.capture();

        this.pressed = false;
    }
    public mouseDrag(x: number, y: number): void {
        // console.log('drag');
        if (this.graphSystem.captured && this.graphSystem.captured.type === 'node') {
            const node = this.graphSystem.captured as Node;
            node.vec.x = x;
            node.vec.y = y;
        }
        this.wasMoved = true;
    }
    public updatePost() {
        if (this.renderer.toggleMST) {
            this.graphSystem.computeMST();
        }
    }

    public render(ctx: CanvasRenderingContext2D, ctxOff: CanvasRenderingContext2D) {
        if (this.needPopulate) {
            this.populate();
        }

        this.divLog.textContent = this.log;
        // console.log(this.divLog.textContent)

        this.renderer.render(ctx, ctxOff);
    }
    // .................... AIP Call
    public exportJSON() { return this.common.exportJSON(); }
    public populate() { return this.common.populate(); }
}

